.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-loader{
    background: #ed0074;
    height: 2px;
    /* opacity: 0.7 !important; */
    z-index: 2;
    position: absolute;
    bottom: 0;
  }

.main-app-container {
  height: 100%;
  background: #ecfeff;
}
.app-container{
  height: 100vh;
  display: flex;
  flex-direction: column;
  /* background: #000; */
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
